import { useEffect, useState } from "react";
import "./FEED-LIST.css"
import Search from "../components/SearchBox/Search";
import {Link} from "react-router-dom";
import axios from "axios"

const FeedList = () => {
    const [feed, setFeed] = useState([]);
    useEffect( async () => {
        const feed = await axios.get("http://api.mbukerepo.tech:5000/resources");
        setFeed(feed["data"]);
    }, [feed]);
    // const onSearch = async(s) => {
    //     const feed = await axios.get(`http://api.mbukerepo.tech:5000/resources?search='${s}'`);
    //     console.log(feed);
    // }
    return (
        <div className="container">
            <div className="feed-list">
            {/* <Search onSearch = {onSearch}/> */}
                <div className="feed-list__items">
                    { feed ?  feed.map(item => (
                        <div className="feed-list__item" key={item._id}>
                            <Link to={"/" + item._id}>
                                <p className="feed-list__item-title">{item.title}</p>
                                <p className="feed-list__item-field">{item.field}</p>
                            </Link>
                        </div>
                    )) : null}
                </div>
            </div>
        </div>
        
    );
}

export default FeedList;